.gym {
  margin-top: 70px;
  margin-bottom: 70px;
}

.gym__description {
  width: 290px;
  margin: 0 auto;
}

.gym__description-title {
  font-family: $alumni-sans-font;
  font-size: 32px;
  line-height: 39px;
  font-weight: 600;
  margin-bottom: 20px;
}

.gym__description-subtitle {
  font-size: 18px;
  line-height: 27px;
  font-weight: 400;
}

.gym__description-text {
  margin-top: 40px;
  margin-bottom: 40px;
}

.gym__description-text--desktop-on {
  display: none;
}

.gym__video-wrapper-decoration {
  width: 320px;
  height: 167px;
  background-color: rgba(20, 54, 122, 0.5);
}

.gym__video-container {
  position: relative;
}

.gym__video-player-button-decoration-ring {
  position: absolute;
  top: -71px;
  left: 3px;
}

.gym__video-player-button-svg {
  position: absolute;
  left: 31px;
  bottom: 27px;
  fill: #ffffff;
  width: 12px;
  height: 12px;
}

.gym__video-player-button-decoration-circle {
  width: 72px;
  height: 72px;
  border-radius: 50px;
  background: linear-gradient(46deg, #003D85 20%, #B80027 88.54%);
  position: absolute;
  left: 14px;
  bottom: 14px;
}

.gym__video-player-button {
  position: absolute;
  top: 125px;
  left: 122px;
}

@media screen and (max-width: 767px){
  .gym__description-text {
    padding-right: 35px;
  }

  .gym__description-background-image-wrapper {
    background-image: url("../img/main/gym1x.webp");
    width: 320px;
    height: 313px;
    background-repeat: no-repeat;
    background-size: 493px auto;
    background-position: -144px;
    margin-bottom: 40px;
  }

  @media screen and (min-resolution: 2dppx){
    .gym__description-background-image-wrapper {
      background-image: url("../img/main/gym2x.webp");
    }
  };

  .gym__video-player-button-decoration-ring {
    width: 69px;
    height: 69px;
  }

  .gym__video-player-button-decoration-circle {
    width: 52px;
    height: 52px;
    left: 11px;
    bottom: 11px;
  }

  .gym__video-player-button-svg {
    left: 23px;
    bottom: 21px;
  }

  .gym__video-wrapper {
    background-image: url("../img/main/gym-video-image1x.webp");
    width: 320px;
    height: 167px;
    background-repeat: no-repeat;
    background-size: 326px auto;
    background-position: 0px -6px;
    z-index: -1;
    position: relative;
  }

  @media screen and (min-resolution: 2dppx){
    .gym__video-wrapper  {
      background-image: url("../img/main/gym-video-image2x.webp");
    }
  };
};

@media screen and (min-width: $vp-768) and (max-width: 1199px) {
  .gym {
    width: 678px;
    margin: 0 auto;
    margin-top: 100px;
    margin-bottom: 190px;
  }

  .gym__description {
    width: 678px;
    margin: 0 auto;
    text-align: center;
  }

  .gym__description-title {
    font-size: 42px;
    line-height: 50px;
    text-align: center;
    margin: 0;
  }

  .gym__description-subtitle {
    text-align: center;
  }

  .gym__description-text {
    text-align: center;
    padding-right: 50px;
    padding-left: 50px;
    margin-top: 50px;
    margin-bottom: 50px;
  }

  .gym__description-background-image-wrapper {
    background-image: url("../img/main/gym1x.webp");
    width: 678px;
    height: 345px;
    background-size: 760px;
    background-position: -90px;
    z-index: -2;
    position: relative;
  }

  @media screen and (min-resolution: 2dppx){
    .gym__description-background-image-wrapper {
      background-image: url("../img/main/gym2x.webp");
    }
  };

  .gym__video-wrapper {
    background-image: url("../img/main/gym-video-image1x.webp");
    width: 266px;
    height: 167px;
    background-repeat: no-repeat;
    background-size: 273px auto;
    background-position: 0px -6px;
    z-index: -1;
    position: relative;
  }

  .gym__video-wrapper-decoration {
    width: 266px;
    position: absolute;
    top: -77px;
    left: 220px;
  }

  @media screen and (min-resolution: 2dppx){
    .gym__video-wrapper  {
      background-image: url("../img/main/gym-video-image2x.webp");
    }
  };

  .gym__video-player-button-decoration-ring {
    width: 69px;
    height: 69px;
    top: -155px;
    left: 198px;
  }

  .gym__video-player-button-decoration-circle {
    width: 52px;
    height: 52px;
    left: 207px;
    bottom: 95px;
  }

  .gym__video-player-button-svg {
    left: 23px;
    bottom: 21px;
  }
};

@media screen and (min-width: $vp-1200){
  .gym {
    width: 1160px;
    margin: 0 auto;
    margin-top: 120px;
    margin-bottom: 238px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    position: relative;
  }

  .gym__description {
    width: 1160px;
    margin: 0 auto;
    text-align: left;
    padding-left: 80px;
  }

  .gym__description-title {
    font-size: 48px;
    line-height: 58px;
    margin-top: 49px;
    margin-bottom: 25px;
  }

  .gym__description-text {
    padding-right: 69px;
    margin-bottom: 0;
    margin-top: 0;
  }

  .gym__description-text--desktop-margin {
    margin-top: 55px;
    margin-bottom: 10px;
  }

  .gym__description-text--desktop-on {
    display: block;
  }

  .gym__description-background-image-wrapper {
    background-image: url("../img/main/gym1x.webp");
    width: 1826px;
    height: 401px;
    background-size: 660px;
    background-repeat: no-repeat;
    z-index: -2;
  }

  @media screen and (min-resolution: 2dppx){
    .gym__description-background-image-wrapper {
      background-image: url("../img/main/gym2x.webp");
    }
  };

  .gym__video-player-button-svg {
    width: 17px;
    height: 17px;
  }

  .gym__video-player-button-decoration-circle {
    left: 20px;
    bottom: -32px;
  }

  .gym__video-player-button-decoration-ring {
    width: 100px;
    height: 100px;
    top: -54px;
    left: 6px;
  }

  .gym__video-player-button {
    position: absolute;
    top: 125px;
    left: 122px;

    &:hover .gym__video-player-button-decoration-ring-stroke  {
     stroke: #102654;
     stroke-opacity: 1;
    }

    &:hover .gym__video-player-button-decoration-circle  {
      background: #ffffff;
    }

    &:hover .gym__video-player-button-svg  {
      fill: #102654;
    }
  }

  .gym__video-container {
    position: absolute;
    left: 441px;
    top: 329px;
  }

  .gym__video-wrapper {
    background-image: url("../img/main/gym-video-image1x.webp");
    width: 364px;
    height: 228px;
    background-repeat: no-repeat;
    background-size: 364px auto;
    background-position: 0px -6px;
    z-index: -1;
    position: relative;
  }

  .gym__video-wrapper-decoration {
    width: 364px;
    height: 228px;
  }

  @media screen and (min-resolution: 2dppx){
    .gym__video-wrapper  {
      background-image: url("../img/main/gym-video-image2x.webp");
    }
  };
};

